import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { UserData } from "../../models/UserData";



@Component({
  selector: 'app-screen',
  templateUrl: './screen.component.html',
  styleUrls: ['./screen.component.css']
})
export class ScreenComponent implements OnInit {
  id = 'qDuKsiwS5xw';
  private player;
  private ytEvent;
  NextToTotalMessages: Array<object>=[];
  FinalTotalMessages:Array<object>=[];


  constructor(private router: Router,private _userData:UserData) { }

  onStateChange(event) {
    this.ytEvent = event.data;
  }
  savePlayer(player) {
    this.player = player;
  }
  playVideo() {
    this.player.playVideo();
  }
  
  pauseVideo() {
    this.player.pauseVideo();
  }
  vidEnded() {
    console.log("video ends");
    this._userData.finalChat=this.FinalTotalMessages;
    console.log("FinalTotalMessages",this.FinalTotalMessages);
	console.log("fnal",this._userData.finalChat);
    setTimeout((router: Router) => {
      this.router.navigate(['./dashboard/final']);
      


  }, 5000);  //5s
      
  }
  ngOnInit() {
    console.log(this._userData.toScreen,"ScreenNextoTotalMessages");
    this.FinalTotalMessages=this._userData.toScreen;
  }

}
