import { Injectable } from "@angular/core";
import { UserData } from "../models/UserData";
import { HttpService } from "../http.service";
import { AppConfigService } from "../config/app.config";
import { Observable } from "rxjs";
import { catchError } from "rxjs/operators";
import { IUserDetails } from "../models";
import { IAccountDetails } from "../models/IAccountDetails";

@Injectable()
export class UserServices{
    readonly pageApi:string="/stryve/stryve";
    constructor(private http: HttpService, 
        private appConfig:AppConfigService, 
        private userdata:UserData) {
    }
    
    //image path
    // imagepath(data:any){
    //     const url: string = `${this.pageApi}/imagepath`;
    //     return this.http.post<any>(url,data).pipe(catchError(this.http.handleError));
    // }

    getText(data:any){
        //http://54.209.224.4/stryve/StryveDialogflow.php?params=hi
        const url: string = `${this.pageApi}/StryveDialogflow.php?params=stryve`;
        return this.http.post<any>(url,data).pipe(catchError(this.http.handleError));
    }
    InputText(data:string){
        //http://54.209.224.4/stryve/StryveDialogflow.php?params=hi
        const url: string = `${this.pageApi}/StryveDialogflow.php?${data}`;
        return this.http.get<any>(url).pipe(catchError(this.http.handleError));
    }
   

}