  public sentmessage: string = '';
  submit() {

      this.items.push(this.sentmessage);
      console.log(this.items);
      this.LastTotalMessages.push({ "text": this.sentmessage, "location": "local" });

      const params = new HttpParams()
          .set('params', this.sentmessage);
      console.log(params.toString());
      let dataParams = params.toString();
      console.log("dataparams", dataParams);
      this.sentmessage="";
      this.hideInput=false;

      this._UserServices.InputText(dataParams).subscribe(data => {
          console.log(data, "recived data .....");
          this.outputText = data;
          console.log("checkingText", this.outputText);
          this.hideInput=true;
        
          var watchVideo=false;

          for (var i = 0; i < data.length; i++) {
            //this.TotalMessages[i];
            if (data[i]['text'] == 'Great!') {
                console.log("true");
                this.LastTotalMessages.push(data[i]);
                watchVideo=true;
                
              //  this.showRocket = true;
                this.hideInput = false;
            }if(watchVideo==false){
                this.LastTotalMessages.push(data[i]);

            }else{
                if (data[i]['text'] != 'Great!') {

                this.NextToTotalMessages.push(data[i]);
                }
                console.log("NextToMessages",this.NextToTotalMessages);
            }
        }
      })
  }

