import { Injectable } from '@angular/core';
import {
  HttpEvent, HttpInterceptor, HttpHandler, HttpRequest, HttpHeaders
} from '@angular/common/http';

import { Observable } from 'rxjs';
import { AppConfigService } from './config/app.config';
import { UserData } from './models/UserData';


/** Pass untouched request through to the next request handler. */
@Injectable()
export class RequestInterceptor implements HttpInterceptor {
  constructor(private _appConfig: AppConfigService,private _appConstants:AppConfigService,private _userData:UserData){
}

intercept(req: HttpRequest<any>, next: HttpHandler):
  Observable<HttpEvent<any>> {

      
      const baseUrl = this._appConfig.apiEndpoint;
      let reqNew: HttpRequest<any> = req.clone({
          url: baseUrl + req.url,
          headers:req.headers              
            .append("Content-Type","application/x-www-form-urlencoded; charset=UTF-8")
            //.append("Accept","application/json")              
            //.append("Authorization", authorization)
      });
    return next.handle(reqNew);
  }
}