import { Injectable } from "@angular/core";
import { HttpService } from "../http.service";
import { AppConfigService } from "../config/app.config";
import { Observable } from "rxjs";
import 'rxjs';
import { IDetails } from "../models";
import { catchError } from "rxjs/operators";
import {Http, Headers, RequestOptions} from '@angular/http';
import { HttpClient, HttpHeaders, HttpClientModule } from '@angular/common/http';

@Injectable()
export class AccessServices{
    readonly pageApi = "/LoginController";
    
    constructor(private _http: HttpService, private _appConfig:AppConfigService) {
    }
    
    getLogin(data:any): Observable<IDetails[]> {
        const url: string = `${this.pageApi}/login`;
        return this._http.post<IDetails[]>(url, data).pipe(catchError(this._http.handleError));
    }
    
    setLogout(data:any): Observable<IDetails[]> {
        const url: string = `${this.pageApi}/signout`;
        return this._http.post<IDetails[]>(url, data).pipe(catchError(this._http.handleError));
    }

}